import actorImage from '../Resource/Image/actors.jpg'

export const getActorTypeClassName = function (type: number) {
    switch (type) {
        case 1:
            return "WARRIOR";
        case 2:
            return "PALADIN";
        case 3:
            return "HUNTER";
        case 4:
            return "ROGUE";
        case 5:
            return "PRIEST";
        case 6:
            return "DEATHKNIGHT";
        case 7:
            return "SHAMAN";
        case 8:
            return "MAGE";
        case 9:
            return "WARLOCK";
        case 10:
            return "DRUID";
        default:
            return "DEFAULT";
    }
};

